@use 'vars' as var;

html {
    font-size: 62.5%;
    box-sizing: border-box;
}

*, *::before, *::after {
    box-sizing: inherit;
}

body {
    font-family: var.$font-family-1;
    font-size: 16px;

    &.fix-body {
        overflow: hidden;
    }
}

.container {
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
}

p {
    font-size: 2rem;
    color: var.$black;
    line-height: 1.5;
}

a {
    text-decoration: none;
}

h1, h2, h3 {
    margin: 0 0 5rem 0;
}

h1 {
    font-size: 4rem;
}

h2 {
    font-size: 3.6rem;
}


h3 {
    font-size: 3rem;
}

img {
    max-width: 100%;
    width: 100%;
    height: auto;
    display: block;
}
