html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
}
body.fix-body {
  overflow: hidden;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

p {
  font-size: 2rem;
  color: #000;
  line-height: 1.5;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

.header {
  background-color: #4CB8B3;
  z-index: 1;
}
.header h1 {
  color: #FFF;
  text-align: center;
}
@media (min-width: 768px) {
  .header h1 {
    margin: 0;
  }
}
.header .header-content {
  padding: 2rem;
}
@media (min-width: 768px) {
  .header .header-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
@media (min-width: 768px) {
  .header-fixed {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    animation: bounce 1.3s;
    box-shadow: 0px 3px 4px 0px rgba(0, 0, 0, 0.75);
  }
}

@media (min-width: 768px) {
  .header-nav {
    display: flex;
    gap: 2rem;
  }
}
.header-nav a {
  color: #FFF;
  font-size: 2.4rem;
  display: block;
  text-align: center;
  margin-bottom: 1rem;
}
.header-nav a:hover {
  color: #FEF46D;
}
@media (min-width: 768px) {
  .header-nav a {
    margin: 0;
  }
}

.progress-container {
  display: none;
  z-index: 2;
  position: fixed;
  top: 0;
  width: 100%;
  height: 8px;
  background-color: gray;
}

.progress-bar {
  height: inherit;
  background-color: #F53756;
  width: 0;
}

.animation {
  animation: fadein 1s;
}

.animation-end {
  animation: fadein-end 0.5s;
}

@keyframes fadein {
  from {
    opacity: 0.2;
  }
  to {
    opacity: 1;
  }
}
@keyframes fadein-end {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
@keyframes bounce {
  0%, 20%, 53%, 80%, 100% {
    transform: translate3d(0, 0, 0);
  }
  40%, 43% {
    transform: translate3d(0, -30px, 0);
  }
  70% {
    transform: translate3d(0, -15px, 0);
  }
  90% {
    transform: translate3d(0, -4px, 0);
  }
  from {
    opacity: 0.2;
  }
  to {
    opacity: 1;
  }
}
.video {
  position: relative;
  max-height: 60rem;
  min-height: 45rem;
  overflow: hidden;
}
@media (min-width: 1200px) {
  .video {
    max-height: 80rem;
  }
}
.video-overlay {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.6);
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.video video {
  width: auto;
  display: block;
}
@media (min-width: 1200px) {
  .video video {
    width: 100%;
  }
}

.video-content {
  z-index: 1;
}
.video-content h2 {
  color: #FFF;
  font-size: 7rem;
  margin: 0;
}
.video-content p {
  background-color: #F53756;
  display: inline-block;
  padding: 1rem 2rem;
  font-size: 2.4rem;
  color: white;
  font-weight: 700;
  margin: 0;
}

.about-fest {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .about-fest {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.fest-content {
  margin-top: 5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media (min-width: 768px) {
  .fest-content {
    margin-top: 0;
  }
}
.fest-content h2 {
  margin-bottom: 2rem;
}
.fest-content .fest-date {
  color: #F53756;
  font-weight: 700;
}

.lineup {
  background-color: #F53756;
  padding: 5rem 0;
}
.lineup h3, .lineup p {
  text-align: center;
  color: #FFF;
}
.lineup .day {
  text-transform: uppercase;
  margin: 5rem 0;
}

@media (min-width: 768px) {
  .stages-container {
    display: grid;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.stages-container p {
  color: #000;
}

.bg-yellow {
  background-color: #FEF46D;
}
.bg-yellow li:nth-child(odd) {
  background-color: #fdee21;
}

.bg-green {
  background-color: #4CB8B3;
}
.bg-green li:nth-child(odd) {
  background-color: #3b9691;
}

.stage {
  padding-top: 2rem;
}
.stage .stage-name:after {
  content: "";
  display: block;
  width: 20rem;
  height: 10rem;
  background-size: 15rem;
  background-repeat: no-repeat;
  background-position: center center;
  margin: 2rem auto 0 auto;
}

.edm .stage-name:after {
  background-image: url(../img/icono_edm.png);
}

.rock .stage-name:after {
  background-image: url(../img/icono_rock.png);
}

.calendar {
  margin: 0;
  padding: 0;
  list-style: none;
}
.calendar li {
  padding: 2.5rem;
  font-weight: 700;
}
.calendar li span {
  font-weight: 400;
}

.gallery {
  padding: 5rem 0;
}
.gallery h3 {
  text-align: center;
}

.gallery-images {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 480px) {
  .gallery-images {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 768px) {
  .gallery-images {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 1200px) {
  .gallery-images {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 2rem;
  }
}
.gallery-images .image {
  transition: all 0.2s ease-out;
}
.gallery-images .image:hover {
  transform: scale(1.1);
  cursor: pointer;
}

.overlay {
  background-color: rgba(0, 0, 0, 0.8);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
}
.overlay .picture {
  max-width: 80rem;
  position: relative;
}
.overlay .picture .picture-bg {
  background-color: rgba(0, 0, 0, 0.6);
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: opacity 0.3s ease-in;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.overlay .picture .picture-bg:hover {
  opacity: 1;
  cursor: pointer;
}

.close-picture-btn {
  width: 10rem;
  height: 10rem;
  border-radius: 50%;
  border: 0.4rem solid rgba(255, 255, 255, 0.4);
  color: rgba(255, 255, 255, 0.4);
  font-size: 4rem;
  display: grid;
  place-items: center;
  transition: all 0.2s ease-out;
  user-select: none;
}
.close-picture-btn:hover {
  border-color: rgba(255, 255, 255, 0.8);
  color: rgba(255, 255, 255, 0.8);
  transform: scale(1.2);
}

.tickets {
  padding: 5rem 0;
}
.tickets h3 {
  text-align: center;
  color: #FFF;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .tickets .tickets-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.pass .pass-name {
  text-align: center;
  font-size: 2.8rem;
  color: #FFF;
}
.pass .header-pass, .pass .footer-pass {
  padding: 0.5rem;
}
.pass .header-pass p, .pass .footer-pass p {
  text-align: center;
  color: #FFF;
  margin: 0;
}
.pass .header-pass {
  background-color: #F53756;
}
.pass .header-pass p {
  text-transform: uppercase;
  font-size: 1.6rem;
}
.pass .body-pass {
  background-color: #FFF;
}
.pass .body-pass ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
.pass .body-pass ul li {
  background-color: #FFF;
  padding: 1rem;
  border-bottom: 0.2rem solid #E1E1E1;
}
.pass .body-pass ul li:last-of-type {
  border-bottom: none;
}
.pass .body-pass ul li:hover {
  transform: scale(1.01);
  background-color: #E1E1E1;
  cursor: pointer;
}
.pass .footer-pass {
  background-color: #752F97;
}
.pass .footer-pass p {
  font-size: 2.4rem;
  font-weight: 700;
}

.footer {
  background-color: #F53756;
  padding: 1rem;
}
.footer p {
  margin: 0;
  text-align: center;
  color: #FFF;
}